import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  position: relative;
  z-index: 1;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledImg = styled.img`
  box-shadow: none;
  border: none;
  background-color: var(--accent);
  border-radius: 20px;
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;


export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
  cursor: pointer;
  background-color: ${({ clicked }) => (clicked ? "red" : "transparent")};
`;

const faqData = [
  {
    question: "What are Ordinals?",
    answer: "Ordinal theory is a method of producing NFTs on the Bitcoin blockchain, with all content stored directly on the chain for optimal transparency and immutability, making it an ideal solution for NFTs.",
  },
  {
    question: "Is this Ordinal art?",
    answer: "Yes, Undead Legion has been creating art since 2023, and as a result, we can efficiently transition into a high-quality Ordinal collection due to our constant work on new art pieces.",
  },
  {
    question: "What is a Proof of Purchase NFT?",
    answer: "It is an ERC721 NFT like any other on OpenSea or other marketplaces. There are some novel changes to account for the Undead Legion functionality, but principally it is no different from any other ERC721 token.",
  },
  {
    question: "Why are you minting on Ethereum?",
    answer: "We chose to mint our tokens on Ethereum to maintain control over the quantity, allowlists, and timings, and offer our users an instantly tradeable asset with a user-friendly experience that they are familiar with.",
  },
  {
    question: "Is there any utility?",
    answer: "We make no guarantees about our future actions regarding Undead Legion post-mint. It's essential to note that we have been operating in this space transparently for several years through Undead Legion. We are not stopping.",
  },
  {
    question: "How can I mint?",
    answer: "To mint your Undead Legion, return to our website on the designated minting day and connect your Ethereum wallet.",
  },
];

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(``);
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 3) {
      newMintAmount = 3;
    }
    setMintAmount(newMintAmount);
  };

  const [faqOpen, setFaqOpen] = useState(true);
  const [selectedQuestion, setSelectedQuestion] = useState(null);

  const toggleFaq = () => {
    setFaqOpen(!faqOpen);
  };

  const selectQuestion = (index) => {
    if (selectedQuestion === index) {
      setSelectedQuestion(null);
    } else {
      setSelectedQuestion(index);
    }
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);
  
  const links = [
    {
      imageUrl: "/config/images/ROUTE.png",
      targetUrl: "https://route.undeadlegion.wtf"
    },
    {
      imageUrl: "/config/images/MINT.png",
      targetUrl: "https://mint.undeadlegion.wtf"
    },
    {
      imageUrl: "/config/images/FAQ.png",
      targetUrl: "https://faq.undeadlegion.wtf"
    },
    {
     imageUrl: "/config/images/DISCORD.png",
     targetUrl: "https://discord.com/invite/MUze9hca2C",
     width: "60px",
     height: "60px"
    },
    {
      imageUrl: "/config/images/TWITTER.png",
      targetUrl: "https://twitter.com/Undead_btc",
      width: "60px",
      height: "60px"
    },
    {
      imageUrl: "/config/images/OPENSEA.png",
      targetUrl: "https://opensea.io/collection/undead_legion",
      width: "60px",
      height: "60px"
    }
  ];
  
  return (
    <s.Screen>
      <s.Container
        flex={1}
        ai={"center"}
        style={{ padding: 24, backgroundColor: "var(--primary)", backgroundImage: `url(${CONFIG.SHOW_BACKGROUND ? "/config/images/bg.png" : null})`, backgroundSize: "100% 100%", backgroundPosition: "center" }}
      >
        <s.SpacerSmall />
        <ResponsiveWrapper flex={1} style={{ padding: 24 }} test>
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg alt={"example"} src={"/config/images/UNDEAD.png"} style={{ position: "absolute", top: "-3.8%", left: "14%", transform: "translateX(-50%)",width: "600px", height: "auto" }} />
            <a href="https://undeadlegion.wtf">
              <img alt="example" src="/config/images/LOGO.png" style={{ position: "absolute", top: "-2.6%", left: "50%", transform: "translateX(-50%)", width: "120px", height: "auto" }} />
            </a>
            <a href="https://mint.undeadlegion.wtf">
              <img alt="example" src="/config/images/left.png" style={{ position: "absolute", top: "50%", left: "25%", transform: "translateX(-50%)",width: "120px", height: "auto" }} />
            </a>
            <a href="https://undeadlegion.wtf">
              <img alt="example" src="/config/images/right.png" style={{ position: "absolute", top: "50%", left: "76%", transform: "translateX(-50%)",width: "120px", height: "auto" }} />
            </a>
          </s.Container>
          <s.SpacerLarge />
          <s.Container
            flex={2}
            jc={"center"}
            ai={"center"}
          >
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--primary-text)",
              }}
            >
            </s.TextDescription>
            <s.SpacerSmall />
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  The sale has ended.
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  You can still find {CONFIG.NFT_NAME} on
                </s.TextDescription>
                <s.SpacerSmall />
                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                </StyledLink>
              </>
            ) : (
              <>
                <s.SpacerXSmall />
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                </s.TextDescription>
                <s.SpacerSmall />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                    </s.TextDescription>
                    <s.SpacerSmall />
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--accent-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {feedback}
                    </s.TextDescription>
                    <s.SpacerMedium />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledRoundButton
                        style={{ lineHeight: 0.4 }}
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          decrementMintAmount();
                        }}
                      >
                        -
                      </StyledRoundButton>
                      <s.SpacerMedium />
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--accent-text)",
                        }}
                      >
                        {mintAmount}
                      </s.TextDescription>
                      <s.SpacerMedium />
                      <StyledRoundButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          incrementMintAmount();
                        }}
                      >
                        +
                      </StyledRoundButton>
                    </s.Container>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                      >
                        {claimingNft ? "BUSY" : "MINT"}
                      </StyledButton>
                    </s.Container>
                  </>
                )}
              </>
            )}
            <s.SpacerMedium />
          </s.Container>
          <s.SpacerLarge />
          <s.Container flex={1} jc={"center"} ai={"center"} style={{ marginLeft: "-300%", width: "10px", marginTop: "10%", marginBottom: "20px" }}>
            <s.SpacerLarge />
            <s.TextDescription
              style={{
                textAlign: "left",
                color: "var(--accent-text)",
                // cursor: "pointer",
              }}
              // onClick={toggleFaq}
            >
            </s.TextDescription>
            {faqOpen &&
              faqData.map((faq, index) => (
                <>
                  <s.TextDescription
                    style={{
                      textAlign: "left",
                      color: "var(--primary-text)",
                      cursor: "pointer",
                      fontSize: "30px",
                    }}
                    onClick={() => selectQuestion(index)}
                  >
                    {faq.question}
                  </s.TextDescription>
                  {selectedQuestion === index && (
                    <div style={{ marginTop: "10px" }}>
                      <s.TextDescription
                        style={{
                          textAlign: "left",
                          color: "var(--accent-text)",
                          margin: "0 auto",
                          maxWidth: "800px",
                          width: "80%",
                          fontSize: "20px",
                        }}
                      >
                        {faq.answer}
                      </s.TextDescription>
                    </div>
                  )}
                  <s.SpacerSmall />
                </>
              ))}
          <div style={{ position: "absolute", top: "0.8%", right: "-16.5%", transform: "translateX(-50%)"}}>
            {links.map((link, index) => (
              <StyledLink   
                onClick={(e) => {
                  e.preventDefault();
                  window.location.href = link.targetUrl;
                }}
              >
                <StyledImg
                  alt={"Custom Image"}
                  src={link.imageUrl}
                  style={{
                    margin: "0 0px",
                    width: link.width || "150px",
                    height: link.height || "auto",
                    backgroundColor: "transparent"
                  }}
                />
              </StyledLink>
            ))}
          </div>            
          </s.Container>
        </ResponsiveWrapper>
        <s.SpacerMedium />
        <s.Container jc={"center"} ai={"center"} style={{ width: "70%" }}>
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
          </s.TextDescription>
          <s.SpacerSmall />
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
          </s.TextDescription>
        </s.Container>
      </s.Container>
    </s.Screen>
  );
}

export default App;
